import { Box, Button, Card, Container, Divider, Link, Typography } from '@mui/material'
import axios from 'axios'
import { useSnackbar } from 'notistack'
import { FC, useRef } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router-dom'
import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import { PureLightTheme } from 'src/theme/schemes/PureLightTheme'

const MigrationPage: FC<void> = () => {
  const uploadInputRef = useRef(null)
  const params = useParams()
  const navigate = useNavigate()
  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()

  const handleSubmit = async () => {
    // 아무 것도 선택안한 경우 return
    if (uploadInputRef.current.files.length === 0) {
      enqueueSnackbar('파일을 선택하여 주십시오.', { variant: 'error' })
      return
    }

    const file = uploadInputRef.current.files[0]
    const formData = new FormData()
    formData.append('file', file)

    try {
      const result = await axios.post(`${process.env.REACT_APP_APIHOST}/migration/upload/${params.id}`, formData, {
        headers: { 'content-Type': 'multipart/form-data', Authorization: authHeader() }
      })

      if (result.data.message !== 'ok') throw new Error()

      enqueueSnackbar('마이그레이션이 완료되었습니다.', { variant: 'success' })
      navigate(`/manage/${params.id}/member`)
    } catch (error) {
      enqueueSnackbar('등록에 실패했습니다. 다시 시도해 주십시오.', { variant: 'error' })
    }
  }

  return (
    <Container maxWidth="lg" sx={{ height: '80vh', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
      <Helmet>
        <title>회원 추가</title>
      </Helmet>
      <Card sx={{ padding: { xs: 2, sm: 4 } }}>
        <Typography variant="h3" align="center" mb={3} color={PureLightTheme.colors.primary.main}>
          회원 추가
        </Typography>
        <Typography marginBottom={2}>
          <Link href={process.env.REACT_APP_MIGRATION_EXAMPLE} target="_blank">
            여기
          </Link>
          를 클릭하시면 양식을 다운로드 받으실 수 있습니다. <br /> 양식에 맞추어 작성하신 후, 엑셀 파일을 업로드하세요.
        </Typography>

        <Divider />

        <Box mt={2} mb={5}>
          <Box height={80}>
            <input
              ref={uploadInputRef}
              type="file"
              accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
              style={{ display: 'none' }}
            />
            <Button
              onClick={() => uploadInputRef.current && uploadInputRef.current.click()}
              variant="outlined"
              fullWidth
              sx={{ height: '100%', display: 'block' }}
            >
              이 곳을 클릭하여 업로드하세요.
              <Typography variant="subtitle1">(.xlsx 확장자만 가능합니다.)</Typography>
            </Button>
          </Box>
        </Box>

        <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText="등록" />
      </Card>
    </Container>
  )
}

export default MigrationPage
